import { Component, OnInit } from '@angular/core'
import { Model_owner_master } from 'src/app/sdk/asset/models/model-_owner-_master'
import { AssetModelOwnerMasterService } from 'src/app/sdk/asset/services/asset-model-owner-master.service'
import { FilterFuctionService } from 'src/app/services/filter-fuction.service'
import { NzMessageService } from 'ng-zorro-antd'
import { NgxSpinnerService } from 'ngx-spinner'
import { Model_category_master } from 'src/app/sdk/asset/models'

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss'],
})
export class ListComponent implements OnInit {
    constructor(
        private assetModelOwnerMasterService: AssetModelOwnerMasterService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    ngOnInit() {
        this.searchData()
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.assetModelOwnerMasterService
            .getAssetModelOwnerMasters(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }




    delete(id) {
        this.spinner.show();
        this.assetModelOwnerMasterService.deleteAssetModelOwnerMastersId(id).subscribe(
            data => {
                this.searchData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }
}
